.map {
    height: 790px;
    width: 100%;
    position: absolute;
    top: 0;
}

.contacts {
    position: relative;
    overflow: hidden;
    background-color: $white;
    padding-top: 100px;
    text-align: center;

    .content-block {
        pointer-events: none;
    }

    .white-block {
        width: 370px;
        text-align: left;
        transition: all .5s;
        cursor: pointer;
        pointer-events: auto;

        &:hover {
            background-color: $gray;
        }

        p {
            font-size: 16px;
            font-weight: 300;
            line-height: 24px;

            a {
                color: $black;
                font-size: 16px;
                font-weight: 300;
                line-height: 24px;
            }
        }

        &__header {
            p {
                font-size: 16px;
                font-weight: 700;
                line-height: 24px;
            }
        }

        &._active {
            min-height: 375px;
            background-color: $gray;
            cursor: default;

            .white-block__content {
                display: block;
            }
        }

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        &__content {
            display: none;
            padding-top: 20px;
        }
    }
}