.footer {
    color: $white;
    background-color: $orange;
    padding-top: 38px;
    padding-bottom: 27px;
    text-align: left;

    &__copyright, &__text, &__tutmee-logo {
        display: inline-block;
        vertical-align: middle;
    }

    &__copyright {
        width: 220px;

        a {
            color: $black;
            font-family: Gilroy;
            font-size: 24px;
            font-weight: 700;
            line-height: 43px;
            text-transform: uppercase;
            padding-bottom: 10px;
        }
    }

    &__text {
        text-align: center;
        width: 490px;
    }

    h4 {
        padding: 0 50px;
    }

    &__tutmee-logo {
        color: $black;
        width: 220px;
        text-align: right;

        p {
            display: inline-block;
            vertical-align: middle;
            margin-right: 10px;
            font-family: Arial;
            font-size: 12px;
            font-weight: 400;
            line-height: 12px;
        }

        img {
            display: inline-block;
            vertical-align: middle;
            width: 45px;
            height: 70px;
        }
    }
}