.header {
    width: 100%;
    position: relative;
    font-size: 0;
    height: 80px;
    overflow: visible;
    z-index: 20;
    position: fixed;
    top: 0;
    left: 0;
    background-color: $white;

    .content-block {
        height: 80px;
    }

    &._scrool {
        .header__phone {
            // transform: translateY(-100%);
            a {
                background-color: $blue;
            }

            a:after {
                box-shadow: 0 0px 0px rgba($blue, 0.25);
            }
        }
    }

    &__logo {
        width: 220px;
        height: 42px;
        display: inline-block;
        vertical-align: top;
        margin-top: 35px;
        margin-right: 20px;
        margin-top: 19px;
    }

    &__phone {
        width: 350px;
        margin-left: auto;
        margin-right: 0;
        transition: all .5s;
        z-index: 1;

        a {
            color: $white;
            font-family: Gilroy;
            font-size: 30px;
            font-weight: 700;
            line-height: 50px;
            text-transform: uppercase;
            display: block;
            position: relative;
            padding-right: 65px;
            transition: all .5s;

            &:after {
                content: '';
                right: 0;
                top: 0;
                width: 50px;
                height: 50px;
                box-shadow: 0 12px 10px rgba($blue, 0.25);
                transition: all .5s;
                background-color: $blue;
                background-image: url("../images/svg/telephone.svg");
                display: block;
                position: absolute;
                background-size: 30px 30px;
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }

    nav {
        vertical-align: top;
        display: inline-block;
        width: calc(700px);
        position: relative;
        z-index: 2;

        ul {
            text-align: justify;

            li {
                font-size: 12px;
                font-weight: 600;
                width: auto;
                text-transform: uppercase;
                display: inline-block;
                text-align: center;
                box-sizing: border-box;
                transition: all .5s;
                padding-left: 0;

                a {
                    color: $gray-text;
                    transition: all .5s;
                    display: block;
                    line-height: 80px;
                    width: 100%;
                    padding: 0 8px;
                    box-sizing: border-box;
                }

                &:hover {
                    background: $orange;

                    a {
                        color: $white;
                    }
                }
            }
        }
    }
}