.clients {
    position: relative;
    overflow: hidden;
    background-color: $white;
    padding-top: 70px;
    padding-bottom: 70px;
    text-align: center;

    .slider-logo {
        padding-top: 69px;
        padding-bottom: 92px;

        // padding-left: 100px;
        // padding-right: 100px;
        // overflow: hidden;

        &__slide {
            width: 180px;
            height: 100px;
            position: relative;
        }

        img {
            position: absolute;
            display: block;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: auto;
            max-width: 100%;
            max-height: 100%;
            height: auto;
        }
    }

    .btn-style {
        max-width: 390px;
    }

    h4 {
        padding-top: 24px;
    }
}