.price {
    position: relative;
    overflow: hidden;
    background-color: $light-gray;
    background-image: url("../images/bg3.jpg");
    padding-top: 70px;
    padding-bottom: 100px;
    text-align: center;
    background-position: center;

    .btn-style {
        max-width: 220px;
        text-align: center;
        display: block;
        margin: 30px auto 10px;
    }

    .radio-wrapper {
        display: inline-block;
        width: 750px;
    }

    .range_wrapper {
        width: 550px;
        position: relative;

        p {
            max-width: 100%;
            color: $gray-text;
        }

        .range {
            width: 550px;
            display: inline-block;
            margin-top: 20px;
            margin-bottom: 43px;
        }

        .range_list {
            position: absolute;
            top: 40px;
            width: 550px;

            span:nth-of-type(2) {
                position: absolute;
                right: -10px;
                left: auto;
            }
        }
    }

    .range_input {
        display: inline-block;
        width: 183px;
        margin-left: 60px;

        input {
            width: 83px;
            padding: 0 15px;
        }

        span {
            font-family: Gilroy;
            font-size: 16px;
            font-weight: 700;
            margin-left: 13px;
        }
    }

    .filter {
        &__header {
            position: relative;

            &:hover {
                .filter__hover {
                    opacity: 1;
                }
            }
        }

        &__hover {
            position: absolute;
            width: 140px;
            box-shadow: 0 3px 16px 11px rgba(152, 152, 152, 0.25);
            background-color: $orange;
            top: 7px;
            left: -135px;
            padding: 14px 15px 14px 15px;
            opacity: 0;
            transition: all .5s;

            p {
                color: $white;
                max-width: 100%;
            }

            &:after {
                content: '';
                position: absolute;
                display: block;
                left: auto;
                right: -10px;
                top: calc(50% - 5px);
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 5px 0 5px 10px;
                border-color: transparent transparent transparent $orange;
            }

            @media all and (max-width: 1105px) {
                display: none;
            }
        }

        &__col {
            width: 50%;
            flex-basis: 50%;
            max-width: 50%;
            min-width: 50%;

            &_1 {
                padding: 30px 50px;
            }
        }

        &__header_info {
            display: flex;
            justify-content: space-around;
            padding: 0 !important;

            h3 {
                padding-top: 30px;
                text-align: left;
                padding-left: 20px;

                &:before, &:after {
                    display: none;
                }
            }

            .line {
                display: flex;
                align-items: center;
                padding-top: 10px;
                padding-bottom: 10px;
                border-bottom: 3px solid $gray;
                margin-right: 20px;
            }

            p {
                padding-top: 0;
                max-width: 100%;
            }

            p:first-of-type {
                flex-basis: 130px;
                width: 130px;
                padding-left: 20px;
            }

            span {
                font-size: 16px;
                line-height: 24px;
                font-weight: 700;
                text-transform: uppercase;
                color: $orange;
            }
        }

        &__header_texture,
        &__header_rastvor {
            .radio_style {
                position: relative;

                &:hover {
                    img {
                        opacity: 1;
                        display: block;
                    }
                }

                img {
                    position: absolute;
                    top: -110px;
                    left: 10px;
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    z-index: 3;
                    opacity: 0;
                    display: none;
                    transition: all .5s;
                }
            }
        }

        &__header_color {
            .radio_style {
                position: relative;
                display: inline-block;
                width: 370px;
                margin-bottom: 10px;

                input {
                    position: absolute;
                    left: 0;
                    top: 0;
                    z-index: 3;
                    display: block;
                    cursor: pointer;
                    opacity: 0;
                    width: 100%;
                    height: 100%;

                    &:checked {
                        & ~ span {
                            i {
                                opacity: 1;
                            }
                        }

                        & ~ label {
                            color: $orange;
                        }
                    }
                }

                span, label {
                    display: inline-block;
                    vertical-align: middle;
                }

                span {
                    position: relative;
                    z-index: 2;
                    transition: 0.3s ease;
                    background-color: $white;
                    text-align: center;
                    width: 38px;
                    height: 38px;
                    background-color: #3a3a3a;
                    border-radius: 50%;

                    i {
                        color: $white;
                        font-family: "FontAwesome";
                        font-size: 17px;
                        font-weight: 400;
                        text-transform: uppercase;
                        line-height: 38px;
                        transition: 0.3s ease;
                        opacity: 0;
                    }
                }

                label {
                    position: relative;
                    z-index: 2;
                    color: $gray-text;
                    font-size: 16px;
                    line-height: 32px;
                    font-weight: 700;
                    text-transform: uppercase;
                    margin-left: 10px;
                }
            }
        }
    }
}

.wall-vis {
    width: 100%;
    height: 100%;
    max-height: 500px;
    position: relative;
    overflow: hidden;
}

.wall {
    width: 600px;

    // height: 500px;
    display: flex;
    flex-wrap: wrap;

    &__brick {
        width: 120px;
        height: 50px;
        background-color: #383838;
        border: 3px solid wheat;
        position: relative;
        transition: all .5s;

        img {
            width: calc(100% + 6px);
            height: calc(100% + 6px);
            position: absolute;
            top: -3px;
            left: -3px;
        }

        &:nth-child(10n + 1),
        &:nth-child(10n + 2),
        &:nth-child(10n + 3),
        &:nth-child(10n + 4),
        &:nth-child(10n + 5) {
            transform: translateX(-50px);
        }

        // &:nth-child(n+8) {
        //     transform: translateX(100px);
        // }
   
    }
}