.leader {
    position: relative;
    overflow: hidden;
    background-color: $light-gray;
    background-image: url("../images/bg5.jpg");
    padding-top: 70px;
    padding-bottom: 100px;
    text-align: center;
    color: $white;

    h2 {
        font-size: 60px;
        padding-bottom: 24px;
        line-height: 1;
        color: $white;
    }

    h4 {
        padding-bottom: 24px;
    }

    p {
        display: inline-block;
        vertical-align: middle;
        width: auto;
        max-width: 400px;
        font-family: "Open Sans";
        font-size: 18px;
        font-weight: 600;
    }

    .btn-style {
        display: inline-block;
        vertical-align: middle;
        width: 193px;
        margin-left: 10px;
        margin-right: 10px;
    }
}