.about {
    position: relative;
    overflow: hidden;
    background-color: $white;
    padding-top: 70px;
    padding-bottom: 100px;
    text-align: center;

    h2 {
        margin-right: auto;
        margin-left: auto;
        max-width: 640px;
    }
}

.sextagon {
    display: inline-block;
    vertical-align: top;
    width: 220px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 24px;
    position: relative;

    &__img-wrap {
        width: 121px;
        height: 140px;
        background-image: url("../images/gray-sextagon.svg");
        background-repeat: no-repeat;
        display: block;
        margin: 0 auto 27px;
        position: relative;

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-height: 56px;
            max-width: 56px;
            width: 100%;
            height: 100%;
        }
    }

    &__text-wrap {
        p {
            font-family: Gilroy;
            font-size: 16px;
            font-weight: 700;
        }
    }

    &_white {
        width: 170px;

        .sextagon__img-wrap {
            background-image: url("../images/white-sextagon.svg");
        }

        .sextagon__text-wrap {
            padding: 0 12px;
        }
    }

    &_orange {
        width: 48%;

        .sextagon__img-wrap {
            background-image: url("../images/orange-sextagon.svg");
        }

        .sextagon__text-wrap {
            p {
                font-size: 16px;
                font-weight: 300;
                line-height: 1.4;

                strong {
                    font-size: 24px;
                    font-weight: 600;
                    display: block;
                    padding-bottom: 32px;
                }
            }
        }
    }
}