.catalog {
    position: relative;
    overflow: hidden;
    background-color: $light-gray;
    padding-top: 70px;
    padding-bottom: 100px;
    text-align: center;

    h3 {
        &:before {
            transform: translate(-700px);
            left: 0;
        }

        &:after {
            transform: translate(700px);
            right: 0;
        }
    }

    .btn-style {
        max-width: 200px;
    }

    .content-block {
        padding: 0;
    }
}

.filter {
    text-align: left;
    margin-bottom: 20px;

    &__header {
        padding-left: 20px;
        padding-right: 20px;
        margin-bottom: 20px;
        font-size: 0;
        margin-left: 10px;
        margin-right: 10px;
        width: 940px;

        p {
            max-width: 60px;
            width: 100%;
            display: inline-block;
            vertical-align: top;
            padding-top: 4px;
            margin-right: 20px;
        }
    }

    &__slide {
        width: 50%;
        display: inline-block;
        vertical-align: top;
    }

    &__tab {
        display: inline-block;
        vertical-align: top;
        cursor: pointer;
        transition: all .5s;
        background-color: $white;

        p {
            max-width: 100%;
            width: auto;
            padding-top: 10px;
            padding-bottom: 8px;
            border-bottom: 3px solid $orange;
            margin: 0 16px;
            color: $gray-text;
            font-family: Gilroy;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            transition: all .5s;
        }

        &._active {
            background-color: $orange;
            cursor: default;

            p {
                color: $white;
            }
        }
    }

    &__content {
        text-align: justify;
        font-size: 0;
    }

    &__item {
        width: 220px;
        height: 400px;
        padding: 20px 15px;
        margin-bottom: 20px;
        text-align: center;
        display: inline-block;
        vertical-align: top;
        margin-right: 10px;
        margin-left: 10px;

        h4 {
            font-size: 18px;
            font-weight: 600;
            max-width: 150px;
            margin: 0 auto;
            min-height: 35px;
        }

        img {
            width: 170px;
            height: 170px;
        }

        p {
            padding-bottom: 13px;
            min-height: 68px;
        }

        .btn-style {
            margin-top: 20px;
        }

        &.rastvor {
            background-color: $blue;
            color: $white;

            .btn-style {
                background-color: $white;
                box-shadow: 0 12px 10px rgba($black, 0.25);

                span {
                    color: $blue;
                }

                &:hover {
                    background-color: $blue;

                    span {
                        color: $white;
                    }
                }
            }
        }
    }

    &__img-wrap {
        padding-bottom: 18px;
        display: block;
    }
}