.reviews {
    position: relative;
    overflow: hidden;
    background-color: $white;
    background-image: url("../images/bg9.jpg");
    padding-top: 90px;
    padding-bottom: 150px;
    text-align: center;

    h2 {
        color: $white;
        position: relative;

        &:before, &:after {
            content: '';
            width: 50vw;
            height: 3px;
            background-color: $white;
            display: block;
            position: absolute;
            transform: translateY(-50%);
            top: 50%;
        }

        &:before {
            transform: translate(-650px);
            left: 0;
        }

        &:after {
            transform: translate(650px);
            right: 0;
        }
    }
}

.slider-reviews {
    &__slide {
        background-color: $white;
        width: 100%;
        padding: 66px 80px 44px;
        text-align: left;

        p {
            font-size: 14px;
            line-height: 1.4;
        }
    }

    &__person-info {
        margin-top: 17px;

        .slider-reviews__photo {
            display: inline-block;
            vertical-align: top;
            width: 59px;
            height: 68px;
            background-image: url("../images/gray-sextagon.svg");
            background-size: 100% 100%;
            background-repeat: no-repeat;
            position: relative;
            margin-right: 20px;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 30px;
                height: 35px;
                transform: translate(-50%, -50%);
            }
        }

        .slider-reviews__text {
            display: inline-block;
            vertical-align: top;

            p {
                strong {
                    font-size: 18px;
                    font-weight: 600;
                    padding: 10px 0;
                    display: block;
                }
            }
        }
    }
}