.steps {
    position: relative;
    overflow: hidden;
    background-color: $white;
    padding-top: 100px;
    padding-bottom: 90px;
    text-align: center;
    color: $white;

    .btn-style {
        max-width: 200px;
    }

    h2 {
        color: $white;
        position: relative;

        &:before, &:after {
            content: '';
            width: 50vw;
            height: 3px;
            background-color: $white;
            display: block;
            position: absolute;
            transform: translateY(-50%);
            top: 50%;
        }

        &:before {
            transform: translate(-750px);
            left: 0;
        }

        &:after {
            transform: translate(750px);
            right: 0;
        }
    }

    .sextagon_white {
        position: relative;

        &:before {
            content: '1';
            position: absolute;
            display: block;
            width: 38px;
            height: 38px;
            background-color: $orange;
            color: #ffffff;
            font-family: "Open Sans";
            font-size: 36px;
            font-weight: 700;
            line-height: 38px;
            text-align: center;
            border-radius: 50%;
            z-index: 2;
            top: 0;
            left: 35px;
        }

        &:after {
            content: '';
            position: absolute;
            display: block;
            width: 40px;
            height: 32px;
            background-image: url("../images/next-arr.png");
            top: 54px;
            left: 93%;
        }

        &:nth-child(3) {
            &:before {
                content: '2';
            }
        }

        &:nth-child(4) {
            &:before {
                content: '3';
            }
        }

        &:nth-child(5) {
            &:before {
                content: '4';
            }
        }

        &:nth-child(6) {
            &:before {
                content: '5';
            }

            &:after {
                display: none;
            }
        }
    }
}

.orange-block {
    width: 100%;
    padding: 44px 100px 55px;
    background-color: rgba($orange, 0.7);
    position: relative;
    margin-top: 16px;
    margin-bottom: -24px;

    h4 {
        color: $white;
    }
}

.step-bg {
    display: block;
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    background-image: url("../images/bg7.jpg");
    background-position: center center;
    animation: 20s scale infinite linear;
    transform-origin: center center;
    will-change: transform;
}

@keyframes scale {
    0% {
        transform: scale(0, 0, 0);
    }

    50% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(0, 0, 0);
    }
}